.admin-panel {
  max-width: 700px;
  margin: 30px auto;
  padding: 20px;
  background: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  font-family: 'Poppins', sans-serif;
}

.admin-panel h2 {
  text-align: center;
  color:#2C3E50; 
  margin-bottom: 20px;
}

.admin-form {
  display: flex;
  flex-direction: column;
  gap: 15px;
  margin-bottom: 30px;
}

.admin-form input[type="text"],
.admin-form input[type="date"],
.admin-form select,
.admin-form textarea {
  padding: 10px;
  border: 1.5px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
  transition: border-color 0.3s ease;
}

.admin-form input[type="text"]:focus,
.admin-form input[type="date"]:focus,
.admin-form select:focus,
.admin-form textarea:focus {
  background-color: #ffffff;
  outline: none;
}

.admin-form textarea {
  resize: vertical;
  min-height: 80px;
  font-family: 'Poppins', sans-serif;
}

.admin-form button {
  background-color: #18BC9C !important;
  color: white;
  border: none;
  padding: 12px;
  border-radius: 5px;
  font-weight: 600;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s ease;
}

.admin-form button:hover {
  background-color: #096653 !important;
}

.admin-event-list h3 {
  color: #2C3E50;
  margin-bottom: 15px;
}

.admin-event-item {
  background: white;
  padding: 12px 15px;
  border-radius: 5px;
  margin-bottom: 10px;
  box-shadow: 0 1px 4px rgb(0 0 0 / 0.1);
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 15px;
}

.admin-event-item strong {
  color: #2C3E50;
}

.admin-event-item div button {
  margin-left: 10px;
  background-color: #FFC107; 
  border: none;
  padding: 6px 12px;
  border-radius: 4px;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.admin-event-item div button:hover {
  background-color: #e0a800;
}

.admin-event-item div button:last-child {
  background-color: #f44336; 
}

.admin-event-item div button:last-child:hover {
  background-color: #811414;
}

.admin-users-list h3,
.admin-registrations-list h3 {
  color: #18BC9C;
  margin-bottom: 20px;
  font-family: 'Poppins', sans-serif;
}

.admin-user-item,
.admin-registration-item {
  font-family: 'Poppins', sans-serif;
  background: #fff;
  box-shadow: 0 1px 4px rgb(0 0 0 / 0.1);
}

.admin-user-item select,
.admin-user-item input[type="checkbox"] {
  margin-left: 8px;
  cursor: pointer;
}
